<script>
  import { onMount } from 'svelte';
  import { tweened } from 'svelte/motion';
  import * as eases from 'svelte/easing';

  import { timeParse, timeFormat } from "d3-time-format";
  import { format } from "d3-format";
  const numFormat = format(",.0f");
  const dateParse = timeParse("%Y-%m-%d");
  const dateFormat = timeFormat("%b %-d");

  import survey from "./data/survey.csv";
  import text from "./data/text.csv";
  import filters from "./data/filters.csv";
  
  import ScatterPlot from "./graphics/scatter_plot.svelte";

  const questions = survey.filter(d => d.question !== "").map(d => ({
    id: d.id,
    question: d.response
  }))
  let selectedQuestion = "1";
  
  $: questionText = questions.filter(d => d.id === selectedQuestion)[0].question;

  $: selectedText = text.filter(d => d.id === selectedQuestion)[0].text;

  const sectors = filters.slice(0, 10);
  let selectedSector = "";

  const sizes = filters.slice(10);
  $: selectedSize = "";

  function clearFilter(type) {
    if(type === "size" && selectedSector !== "") {
      selectedSize = ""
    }
    if(type === "sector" && selectedSize !== "") {
      selectedSector = ""
    }
    selectedField = changeFilter();
    setTimeout(() => {
      pymChild.sendHeight();
    }, 50);
  }

  const responses = survey.filter(d => d.question === "" && d.response !== "").map(d => {
    let output = {
      id: d.id,
      response: d.response,
    };

    for(let i = 0; i < 14; i++) {
      if(d[`sector_${i + 1}`] !== "") {
        output[`sector_${i + 1}`] = +(d[`sector_${i + 1}`].replace("%", ""))
      }
    }

    return output;
  });

  $: selectedResponses = responses.filter(d => d.id === selectedQuestion);
  $: questionSubtitle = () => {
    if(selectedSector !== "") {
      return "Sector: " + selectedSector;
    }
    if(selectedSize !== "") {
      return "Company size: " + selectedSize;
    }
    return "Overall"
  };

  $: selectedField = "sector_1";

  function changeSelected(dir) {
    if(dir) {
      selectedQuestion = (+selectedQuestion + dir).toString()
    }
    selectedField = "sector_1";
    selectedSector = "";
    selectedSize = "";
    setTimeout(() => {
      pymChild.sendHeight();
    }, 50);
  }

  function changeFilter() {
    let response = "";
    if(selectedSize === "" && selectedSector === "") {
      response = "sector_1";
    }
    else if(selectedSize !== "") {
      response = "sector_" + sizes.filter(d => d.section === selectedSize)[0].id
    }
    else if(selectedSector !== "") {
      response = "sector_" + sectors.filter(d => d.section === selectedSector)[0].id
    }
    return response;
  }

  $: chartData = selectedResponses.map(d => ({
    ...d,
    key: d.response,
    value: d[selectedField]
  }));

  export let pymChild = {};
  export let styleNs = false; // serif or san-serif;
  export let title = "NSMG-000";

	onMount(() => {
			pymChild.sendHeight();
  });
</script>

<svelte:head>
  <title>{title}</title>
  <link
  href="https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700&display=swap"
  rel="stylesheet" />
</svelte:head>

<div class="embed-container" class:styleNs>

  <div class="chart-container">
    <header>
      <h3>{questionText}</h3>
      <h4>{questionSubtitle()}</h4>
      <p>{selectedText}</p>
    </header>
    <ScatterPlot data={chartData} />
    <div class="source-text">Source: New Statesman survey</div>

  <label for="selQ">Change question:</label>
  <select id="selQ" bind:value={selectedQuestion} on:change={() => changeSelected()}>
    {#each questions as question}
      <option value="{question.id}">{question.question}</option>
    {/each}
  </select>
  <button disabled={selectedQuestion === "1"} on:click={() => changeSelected(-1)} aria-label="previous">&lt;</button>
  <button disabled={selectedQuestion === "15"} on:click={() => changeSelected(1)} aria-label="next">&gt;</button>

  <div class="controls">
  {#if selectedResponses[0].sector_2}
    <div>
      <label for="selSector">Filter by sector:</label>
      <select id="selSector" bind:value={selectedSector} on:change={() => clearFilter("size")}>
        <option value="">Overall</option>
        {#each sectors as sector}
          <option value="{sector.section}">{sector.section}</option>
        {/each}
      </select>
    </div>
  {/if}
  {#if selectedResponses[0].sector_12}
    <div>
      <label for="selSize">Select company size:</label>
      <select id="selSize" bind:value={selectedSize} on:change={() => clearFilter("sector")}>
        <option value="">Overall</option>
        {#each sizes as size}
          <option value="{size.section}">{size.section}</option>
        {/each}
      </select>
    </div>
  {/if}
  </div>

  </div>

</div>

